<style lang="less">
.css-index-com{
    width: 100%;
    height: 100%;
    position: relative;
    .css-box{
        width: 90%;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        color: #ffffff;
        overflow-y: scroll;
        li{
            margin: 3px 0;
            .box-title{
