<template>
    <v-table
        is-horizontal-resize
        :is-loading="false"
        style="width:100%"
        :columns="columns"
        :table-data="tableData"
        row-hover-color="#eee"
        row-click-color="#edf7ff"
        :cell-edit-done="cellEditDone">
    </v-table>
</template>

<style>
    .cell-edit-color{
        color:#2db7f5;
        font-weight: bold;
    }
</style>

<script>
export default {
    data() {
        return {
            loading: false,
            tableData: [
                {"name":"","tel":"156*****1987","hobby":"","address":"56917"},
                {"name":"","tel":"182*****1538","hobby":"","address":"122"},
                {"name":"","tel":"161*****0097","hobby":"","address":"739"},
                {"name":"","tel":"197*****1123","hobby":"","address":"24"},
                {"name":"","tel":"183*****6678","hobby":"","address":"867-871"}
            ],
            columns: [
                {field: 'name', title:'', width: 1, titleAlign: 'center',columnAlign:'center',isEdit:true,
                formatter: function (rowData,rowIndex,pagingIndex,field) {
                    return `<span class='cell-edit-color'>${rowData[field]}</span>`;
                },isResize:true},
                {field: 'tel', title: '', width: 1, titleAlign: 'center',columnAlign:'center',isEdit:true,isResize:true},
                {field: 'hobby', title: '', width: 1, titleAlign: 'center',columnAlign:'center',isEdit:true,isResize:true},
                {field: 'address', title: '', width: 1, titleAlign: 'center',columnAlign:'left',isEdit:true,isResize:true}
            ]
        }
    },
    methods:{
        // 
        cellEditDone (newValue, oldValue, rowIndex, rowData, field){
            this.tableData[rowIndex][field] = newValue;
            console.log(newValue, 'newValue')
            console.log(oldValue, 'oldValue')
            console.log(rowIndex, 'rowIndex')
            console.log(rowData, 'rowData')
            console.log(field, 'field')
            console.log(this.tableData, 'this.tableData')
        }
    }
}
</script>


